.grid-container {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 200px;

  }
  .grid-item {
    background-color: rgba(255, 255, 255, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 30px;
    text-align: center;
  }
  .imagenTamanio{
    width: 150px;
    height: 150px;
}